.layout {
  min-height: 100vh;
  margin: 0;
  padding: 12px;
  border: 18px solid #DEEAF3;
  background-color: #00adb5;
  color: #fff;
}
.layout-header {
  margin: 0px;
  padding: 18px 12px;
  display: flex;
  justify-content: baseline;
  align-content: center;
}
.layout-header a:link,
.layout-header a:visited,
.layout-header a:hover,
.layout-header a:active {
  color: #fff;
}
.layout-header > div {
  margin: 0;
  padding: 0;
  width: 100%;
}
.layout-header .main {
  text-align: left;
  font-size: 2rem;
}
.layout-header .sub {
  text-align: right;
  font-size: 1.2rem;
}
.layout-content {
  padding: 0px 30%;
  font-size: 1rem;
}
@media (max-width: 992px) {
  .layout-content {
    padding: 0px 10%;
  }
}
.layout-footer {
  font-size: 0.8rem;
  padding-top: 20px;
  text-align: center;
  color: #888;
}
.layout-reversal {
  border: 18px solid #00adb5;
  background-color: #fff;
  color: #222831;
}
.layout-reversal .layout-header a:link,
.layout-reversal .layout-header a:visited,
.layout-reversal .layout-header a:hover,
.layout-reversal .layout-header a:active {
  color: #222831;
}
